
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  background-color:  #eff2f6;
}
#app h1 {
  text-align: center;
  padding-top: 1em;
}
#newTask {
  margin-top: 2em;
  width: 98%;
  height: 2em;
  color: #ffffff;
  font-weight: bold;
  background-color: #2a4a75;
  border: 3px solid #076ff8;
  border-radius: 1em;
}
.myTasks, .completedTasks {
  border: 3px solid #adbdd1;
  border-radius: 1em;
  background-color: #dee5ed;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  margin: 0.5em;
  padding: 1em;
}

.taskItem > input, .taskItem > label {
  padding: 15px;
}
.taskItem button {
  padding: 0;
  float: right;
}
.taskItem #delete {
  color: red;
}
.taskItem #dueDate {
  color: grey;
  float: right;
}

.optionsView {
  display: grid;
  grid-template-columns:2fr 1fr;
  -webkit-column-gap: 10px;
          column-gap: 10px;
}
.optionsView #noteLabel {
  grid-column:1;
  grid-row:1;
}
.optionsView #taskNote {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  grid-column:1;
  grid-row: 2/5;
}
.optionsView #dueLabel {
  grid-column:2;
  grid-row:1;
}
.optionsView #taskdueDate {
  grid-column:2;
  grid-row: 2;
}
.optionsView #priorLabel {
  grid-column:2;
  grid-row: 3;
}
.optionsView #taskPriority {
  grid-column:2;
  grid-row: 4;
}
.optionsView #saveButton {
  grid-column: 2;
  grid-row: 5;
}


.done #taskDesc {
  text-decoration: line-through;
}
.done > input, .done > label {
  padding: 15px;
}
.done button {
  padding: 0;
  float: right;
}
.done #delete {
  color: red;
}
